import React, { useEffect, useState } from "react";
import "./hero.scss";

function Hero() {
  const [loaded, setloaded] = useState('notloaded');
  useEffect(() => {
    window.addEventListener("load", handleload);
    return () => {
      window.removeEventListener("load", handleload);
    };

  }, []);
  function handleload() {
    setTimeout(() => {
      setloaded('loaded');
    }, 2700);
  }
  return (
    <div className={"hero " + loaded} id='home'>
      <p className="hi">Hi, My Name Is</p>
      <h1 className="name">George Tefnadze</h1>
      <h1 className="work">I Build Things For The Web</h1>
      <p className="desc">
        I'm Student Who Studies Front-End Web Development And Loves To Learn New
        Things Everyday. I'm Focused On Learning New Technologies For Building
        Web Apps
      </p>
      <a href="#" className="check">
        Check Out My Projects!
      </a>
    </div>
  );
}

export default Hero;
