import React, { useState, useEffect } from "react";

import "./loader.scss";

function Loader() {
  const [load, setload] = useState('loader')
  useEffect(() => {
    window.addEventListener("load", handleload);

    return () => {
      window.removeEventListener("load", handleload);
    };
  }, []);

  function handleload() {
    setTimeout(() => {
      setload('loader loaded')
        setTimeout(() => {
          setload('loader loaded done')
        }, 500);
    }, 2200);
  }

  return (
    <div className={load}>
      <div className="logowrap">
        <div className="leftline line"></div>
        <div className="rightline line"></div>
        <div className="topline line"></div>
        <div className="bottomline line"></div>
      </div>
      <p className="g">G</p>
    </div>
  );
}

export default Loader;
