import "./project.scss";

import Fade from "react-reveal/Fade";

function Project(prop) {
  function dir() {
    if (prop.direction === "left") {
      return (
        <Fade left delay={200}>
          <div className="left">
            <a href={prop.link} className="color" target='_blank'></a>
            <img src={prop.img} alt="" className="projectimg" />
          </div>
        </Fade>
      );
    } else {
      return (
        <Fade right delay={200}>
          <div className="left">
            <a href={prop.link} className="color" target='_blank'></a>
            <img src={prop.img} alt="" className="projectimg" />
          </div>
        </Fade>
      );
    }
  }
  function dir2() {
    if (prop.direction === "left") {
      return (
        <Fade right delay={400}>
          <div className="desc">
            <p className="desctext">{prop.desc}</p>
          </div>
        </Fade>
      );
    } else {
      return (
        <Fade left delay={400}>
          <div className="desc">
            <p className="desctext">{prop.desc}</p>
          </div>
        </Fade>
      );
    }
  }
  return (
    <div className="projectwrap" id={prop.direction}>
      {dir()}
      <div className="right">
        <Fade top delay={400}>
          <p className="featured">
            Featured Project {prop.unfinished && <span>(Not Complete)</span>}
          </p>
        </Fade>
        <Fade top delay={600}>
          <h2 className="title">{prop.title}</h2>
        </Fade>
        {/* here */}
        {dir2()}
        <div className="languages"></div>
        <Fade bottom delay={800}>
          <a href={prop.link} className="visitbtn" target='_blank'>
            Visit
          </a>
        </Fade>
      </div>
    </div>
  );
}

export default Project;
