import React, { useState, useEffect } from "react";

import "./header.scss";
import { ReactComponent as Logo } from "./../../img/logo.svg";

function Header() {
  const [scroll, setscroll] = useState(0);
  const [loaded, setloaded] = useState("notloaded");
  const [burger, setburger] = useState('notactive');

  useEffect(() => {
    window.addEventListener("scroll", handlescroll, { passive: true });
    window.addEventListener("load", handleload);

    return () => {
      window.removeEventListener("scroll", handlescroll, { passive: true });
      window.removeEventListener("load", handleload);
    };
  }, []);

  function handleload() {
    setTimeout(() => {
      setloaded("loaded");
    }, 2700);
  }

  function handlescroll() {
    const position = window.scrollY;
    setscroll(position);
  }

  function handleburger() {
    if(burger == 'notactive') {
      setburger('active')
    } else {
      setburger('notactive')
    }
  }
  return (
    <>
    <header className={scroll >= 150 ? "active" : ""} id={loaded}>
      <nav>
        <a href="#" className="logowrap">
          <Logo className="logo"></Logo>
        </a>
        <div className="links">
          <a href="#home">
            <span>01.</span>
            Home
          </a>
          <a href="#about">
            <span>02.</span>
            About
          </a>
          <a href="#projects">
            <span>03.</span>
            Projects
          </a>
          <a href="#">
            <span>04.</span>
            Contact
          </a>
        </div>
        <div className={`burgericon ${burger}`} onClick={handleburger}>
          <div className="line"></div>
          <div className="line"></div>
          <div className="line"></div>
        </div>
      </nav>
    </header>
    <div className={`burgermenu ${burger}`}>
    <div className="links">
          <a href="#home" onClick={handleburger}>
            <span>01.</span>
            Home
          </a>
          <a href="#about" onClick={handleburger}>
            <span>02.</span>
            About
          </a>
          <a href="#projects" onClick={handleburger}>
            <span>03.</span>
            Projects
          </a>
          <a href="#" onClick={handleburger}>
            <span>04.</span>
            Contact
          </a>
        </div>
    </div>
    </>
  );
}

export default Header;
