import React, { useRef, useEffect, useState } from "react";

import "./projects.scss";

import Project from "./element/project.js";
import Fade from "react-reveal/Fade";

//img
import realestate from "./../../img/realestate.png";
import galaxynft from "./../../img/galaxynft.png";
import spotify from "./../../img/spotify.png";
import wouldyourather from "./../../img/wouldyourather.png";

function Projects() {
  return (
    <div className="projects" id="projects">
      <div className="titlewrap">
        <Fade top delay={200}>
          <h1>
            <span>03.</span>Projects I've Built
          </h1>
        </Fade>
        <Fade right delay={400}>
          <span className="line"></span>
        </Fade>
      </div>
      <Project
        direction="left"
        img={realestate}
        title="Real Estate"
        desc="A minimalistic, White-Green-Black Theme clean looking Real Estate mark-up website"
        link="https://anubito.github.io/Real-Estate/"
      />
      <Project
        direction="right"
        img={galaxynft}
        title="Galaxy NFT"
        desc="NFT Trading Website Mark-up with extraordinary galaxy and neon effects"
        link="https://anubito.github.io/Galaxy-NFT/"
      />
      <Project
        direction="left"
        img={spotify}
        unfinished="true"
        title="Spotify Clone"
        desc="Functional spotify one to one clone (still in development)"
        link="https://spotify-clone-c682a.web.app/"
      />
      <Project
        direction="right"
        img={wouldyourather}
        unfinished="true"
        title="Would You Rather Game"
        desc="Would you rather game that shows how much people thinks like you, questions history and gives opartunity to create your own questions (still in development)"
        link="https://wouldyourather-b7f4c.web.app/"
      />
    </div>
  );
}

export default Projects;
