import React, { useRef, useState } from "react";

import { Swiper, SwiperSlide } from "swiper/react";
import { EffectCube, Pagination, Autoplay } from "swiper";

import Fade from "react-reveal/Fade";

import "swiper/css";
import "swiper/css/effect-cube";
import "swiper/css/pagination";

import "animate.css/animate.min.css";

import "./about.scss";

//images
import js from "./../../img/js.png";
import react from "./../../img/React.png";
import vue from "./../../img/Vue.png";
import firebase from "./../../img/firebase.webp";

function about() {
  return (
    <div className="about" id="about">
      <div className="titlewrap">
        <Fade top>
          <h1>
            <span>02.</span>About Me
          </h1>
        </Fade>
        <Fade right>
          <span className="line"></span>
        </Fade>
      </div>
      <div className="inner">
        <div className="left">
          <Fade top delay={200}>
            <p>
              Hello! My name is George and i love creating things for WEB. My
              interest in web development started about 3 years ago when I went
              to academy
            </p>
          </Fade>
          <Fade top delay={400}>
            <p>
              Fast-forward to today, I have done some freelance projects but
              unfortunately i haven't worked in any corporation or studio and i
              hope that changes some day
            </p>
          </Fade>
          <Fade top delay={600}>
            <p>Here are languages I've been using to create things:</p>
          </Fade>
          <div className="languageswrap">
            <Fade right delay={800}>
              <div className="languages">
                <p className="highlited">Languages</p>
                <p>HTML</p>
                <p>CSS/SCSS</p>
                <p>JavaScript (ES6)</p>
              </div>
            </Fade>
            <Fade right delay={1000}>
              <div className="frameworks">
                <p className="highlited">Frameworks</p>
                <p>React 18</p>
                <p>Vue 3</p>
              </div>
            </Fade>
            <Fade right delay={1200}>
              <div className="back">
                <p className="highlited">Back-End</p>
                <p>Firebase</p>
                <p>PHP</p>
                <p>SQL</p>
              </div>
            </Fade>
          </div>
        </div>
        <Fade top delay={1400}>
        <div className="right">
          <Swiper
            effect={"cube"}
            grabCursor={true}
            cubeEffect={{
              shadow: true,
              slideShadows: false,
              shadowOffset: 20,
              shadowScale: 0.94,
            }}
            autoplay={{
              delay: 4000,
            }}
            loop={true}
            modules={[EffectCube, Autoplay]}
            className="mySwiper"
          >
            <SwiperSlide>
              <img src={js} />
            </SwiperSlide>
            <SwiperSlide>
              <img src={react} />
            </SwiperSlide>
            <SwiperSlide>
              <img src={vue} />
            </SwiperSlide>
            <SwiperSlide>
              <img src={firebase} />
            </SwiperSlide>
          </Swiper>
          <div className="square"></div>
        </div>

        </Fade>
      </div>
    </div>
  );
}

export default about;
